import React, {Component} from 'react'
import SignupDisplay from './SignupDisplay'
import toastr from 'toastr'
import axios from 'axios'

// const url = 'http://localhost:3000/login'
// const url = 'https://childman.herokuapp.com/'

class Signup extends Component{

    state = {
        signup:{username:"User 1"},
        loading:false
    }

    onChange = (e) => {
        const field = e.target.name
        const value = e.target.value
        const {signup} = this.state
        signup[field] = value
        this.setState({signup})
    }

    createUser = (e) => {
        e.preventDefault()
        const {signup} = this.state
        if(signup.password !== signup.password2) {
            return toastr.error('tu contraseña no coincide')
        }
        axios.post('https://childman.herokuapp.com/signup', signup)
        .then(user=>{
            console.log(user)
            toastr.success("¡Te haz registrado con exito!")
        })
        .catch(e=>toastr.error("No se puede registrar, intente mas tarde"))
    }

    render(){
        const {signup, loading} = this.state
        return(
           <SignupDisplay 
              signup = {signup}
              loading = {loading}
              onChange = {this.onChange}
              createUser = {this.createUser}
           />
        )
    }
}

export default Signup