import React from 'react'
import { Input, Button } from 'antd'


const SignupDisplay = ({signup, loading, onChange, createUser }) => {
  return(
    <div>
       <form onSubmit={createUser} style={{width:600, margin:"0 auto", padding:20}}>
                <h2>Registrate</h2>
                <p>
                    <Input 
                        name="username"
                        type="text"
                        onChange={onChange}
                        value={signup.username}
                        placeholder="Tu nombre de usuario" 
                    />
                    
                </p>
                <p>
                <Input 
                    name="email"
                    type="email"
                    onChange={onChange}
                    value={signup.email}
                    placeholder="Tu correo" 
                    />    
                </p> 
                <p>
                <Input 
                    name="password"
                    type="password"
                    onChange={onChange}
                    value={signup.password}
                    placeholder="Tu contraseña" 
                    />    
                </p>  
                <p>
                <Input 
                    name="password2"
                    type="password"
                    onChange={onChange}
                    value={signup.password2}
                    placeholder="Repite tu contraseña" 
                    />    
                </p>   
                <Button loading={loading} type="primary" htmlType="submit" >Registrarme</Button>
            </form>
    </div>
  )
}

export default SignupDisplay