import React from 'react'
import {Input, Button} from 'antd'

const LoginDisplay = ({auth, loading, onChange, login}) => {
  return(
    <div className='auth'>
        <form onSubmit={login} style={{width:300, margin:"0 auto", padding:20}} className='form'>
                <h2 className='title'>Inicia sesión</h2>
                <p>
                <Input 
                    name="email"
                    type="email"
                    onChange={onChange}
                    value={auth.email}
                    placeholder="Tu correo" 
                    />    
                </p> 
                <p>
                <Input 
                    name="password"
                    type="password"
                    onChange={onChange}
                    value={auth.password}
                    placeholder="Tu contraseña" 
                    />    
                </p>   
                <Button loading={loading} type="primary" htmlType="submit" >Inicia sesión</Button>
                <br/>
                <br/>
                <p>Aún no tienes cuenta?</p>
                <Button type='primary' href='/Signup'>Registrate</Button>
            </form>
    </div>
  )
}

export default LoginDisplay