import React, {Component} from 'react'
import ProfileData from './ProfileData'
import axios from 'axios'
import toastr from 'toastr'
import {getUserDesigns} from '../../services/userService'
import Gallery from './DesignGallery';


class Profile extends Component{

    state = {
        user:{},
        designs:[]
    }

    componentWillMount(){
        const user = JSON.parse(localStorage.getItem('user'))
        if(!user) return this.props.history.push('/login')
        this.setState({user})
        //pedimos las fotos correspondientes al usuario
        this.getDesigns()
    }

    getDesigns = () => {
        getUserDesigns()
        .then(designs=>{
            this.setState({designs})
        })
        .catch(e=>toastr.error("no se pueden mostrar tus diseños"))
    }


    getPrivateInfo = () => {
        axios.get('https://childman.herokuapp.com/profile', {
            headers:{
                "Authorization" : localStorage.getItem('token') 
            }
        })
        .then(res=>{
            console.log(res)
        })
        .catch(e=>toastr.error("algo falló", e.message))
    }

    uploadPhoto = () => {
        this.refs.input.click()
    }

    onChangeFile = (e) => {
        console.log(e.target.files[0])
        //uploadPic(e.target.files[0])
        .then(design=>console.log(design))
        .catch(e=>toastr.error('Error'))
    }

    render(){
        const {user, designs} = this.state
        return(
            <div>
                <ProfileData 
                    user={user}
                    onChangeFile={this.onChangeFile}
                />            
                <Gallery 
                    designs={designs} 

                />
            </div>
        )
    }
}

export default Profile