import React from "react";
import toastr from 'toastr'
import { Select, Upload, Icon, Input, Button } from "antd";

const NewCloth = ({handleSelect, onChange, onChangeFile, onSubmit, loading }) => {
  const Option = Select.Option;

  const Dragger = Upload.Dragger;

  const props = {
    name: "pictures",
    multiple: true,
    action: "//jsonplaceholder.typicode.com/posts/",
    onChange(info) {
      const status = info.file.status;
      if (status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (status === "done") {
        toastr.success(`${info.file.name} file uploaded successfully.`);
      } else if (status === "error") {
        toastr.error(`${info.file.name} file upload failed.`);
      }
    }
  };

  return (
    <div className='inputs'>
      <h2 className='title'>Crea un nuevo articulo</h2>
      <form onSubmit={onSubmit}>
        <Select
          name="categoria"
          defaultValue="Categoria"
          style={{ width: 120 }}
          onChange={handleSelect}
        >
          <Option value="Mujer">Mujer</Option>
          <Option value="Hombre">Hombre</Option>
          <Option value="Niños">Niños</Option>
        </Select>
        <br />
        <Select
          name="tallas"
          defaultValue="Talla"
          style={{ width: 120 }}
          onChange={handleSelect}
        >
          <Option value="S">S</Option>
          <Option value="M">M</Option>
          <Option value="L">L</Option>
        </Select>
        <br />
        <Input
          onChange={onChange}
          placeholder="Nombre"
          name="name"
          type="text"
          style={{ width: 600, margin: "0 auto", padding: 20 }}
        />
        <br />
        <Input
          onChange={onChange}
          placeholder="Price"
          name="price"
          type="number"
          style={{ width: 600, margin: "0 auto", padding: 20 }}
        />
        <br />
        <Input
          onChange={onChange}
          placeholder="Description"
          name="description"
          type="text"
          style={{ width: 600, margin: "0 auto", padding: 20 }}
        />
        <br />
        <Dragger {...props} accept="image/*" onChange={onChangeFile}>
          <p className="ant-upload-drag-icon">
            <Icon type="inbox" />
          </p>
          <p className="ant-upload-text">
            Click or drag file to this area to upload
          </p>
          <p className="ant-upload-hint">
            Support for a single or bulk upload. Strictly prohibit from
            uploading company data or other band files
          </p>
        </Dragger>
        <br />
        <Button loading={loading} type="primary" htmlType="submit">
          New
        </Button>
      </form>
    </div>
  );
};

export default NewCloth;
