import React, { Component } from "react";
// import froalaEditor from 'react-froala-wysiwyg'

class newCloth extends Component {

  function() {
    ('div#froala-editor').froalaEditor({
      dragInline: false,
      toolbarButtons: ['bold', 'italic', 'underline', 'insertImage', 'insertLink', 'undo', 'redo'],
      pluginsEnabled: ['image', 'link', 'draggable']
    })
  };

  render() {
    return (
      <div>
        <div id="froala-editor fr-view">
          <h3>Click here to edit the content</h3>
          <p>
            <img
              id="edit"
              class="fr-fil fr-dib"
              src="https://http2.mlstatic.com/D_Q_NP_722309-MLM27794622417_072018-Q.jpg"
              alt="Old Clock"
              width="300"
            />
          </p>
          <p>
            The image can be dragged only between blocks and not inside them.
          </p>
        </div>
      </div>
    );
  }
}

export default newCloth;
