import React from 'react'
import {Switch, Route} from 'react-router-dom'
import Signup from './components/auth/SignupContainer'
import Login from './components/auth/LoginContainer'
import Profile from './components/users/Profile'
import Admin from './components/users/Admin'
import Diseña from './components/users/Diseña'
import Home from './components/homePage/Home'
import Mujer from './components/categorias/Mujer'
import Hombre from './components/categorias/Hombre'
import Niños from './components/categorias/Niños'

const Routes = () => {
  return(
    <Switch>
      <Route path='/signup' component={Signup} />
      <Route path='/login' component={Login} />
      <Route path='/profile' component={Profile} />  
      <Route path='/admin' component={Admin} />            
      <Route path='/diseña' component={Diseña} />      
      <Route path='/mujer' component={Mujer} />
      <Route path='/hombre' component={Hombre} />
      <Route path='/niños' component={Niños} />
      <Route path='/' component={Home} />
    </Switch>
  )
}

export default Routes